.Header{
  border: 1px solid;
  padding: 0 15px;
  margin: 10px 15px;
  color: #fff;
  background: tomato;
  overflow: auto;
}

.BodyContainerMain {
  padding: 0 15px;
  margin: 10px 15px;
  overflow: auto;
}

ul.Nav{
  list-style-type: none !important;
  margin: 0; padding: 0;
  font-weight: bold;
  float: right;
}
.Nav li{
  float: left;
  margin-right: 10px;
  cursor: pointer;
  color:#000;
  padding: 5px 10px;
}


.Nav li:hover{
  background: #ff7547;
  color: #fff;;
}

li .active{
  color: wheat;
}

.loadingIcon{
  display: block;
  width: 200px;
  margin: 10px auto;
}

.BodyContainer{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

}

.bookTile{
  flex-basis: 18%;
  border: 1px solid #d0d0c1;
  padding: 7px;
  margin: 10px 0;
  position: relative;
}

@media screen and (max-width: 1000px){
  .bookTile{
    flex-basis: 45%;
    border: 1px solid #d0d0c1;
    padding: 7px;
    margin: 10px 0;
    position: relative;
  }
}

@media screen and (max-width: 500px){
  .bookTile{
    flex-basis: 90%;
    border: 1px solid #d0d0c1;
    padding: 7px;
    margin: 10px 0;
    position: relative;
  }
}

.imgContainer{
  display: block;
  width: 80%;
  margin: 7px auto;
  height: 150px;
  position: relative;
  overflow: hidden;
}
.bookImage{
  position: absolute;
  width:100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

h2, h3, h4, h5, p{
  margin: 5px;
  padding: 0;
  font-weight: 400;
}

h4{font-weight: 600;}

.buyButton{
  display: block;
  margin: 5px auto;
  text-align: center;
  background: #86bc25;
  box-shadow: 1px -2px 4px 2px #777777;
  border-radius: 10px;
  margin: 5px;
  padding: 0 15px 5px 15px;
  position: absolute;
  bottom: 5px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
}
.buyButton:hover{color: #fff; background-color: #1a485e;}

.addtocart{
  display: block;
  width:100px;
  margin: 10px auto;
  text-align: center;
  background: #86bc25;
  box-shadow: 1px -2px 4px 2px #777777;
  border-radius: 10px;
  padding: 5px 15px 10px 15px;
  cursor: pointer;
  font-weight: 500;
}

.addtocart:hover{color: #fff; background-color: #1a485e;}

.bookDtl{
  width: 100px;
  box-shadow: 1px 2px 4px 1px #777777;
  margin: 10px;
  float: left;
}

.fltLeft{margin-top: 7px;}

.priceTag{position: absolute; right: 10px; bottom: 5px;}
.cartItem{
  position: relative;
  border: 1px solid #d0d0c1;
  padding: 5px 15px;
  margin: 5px 20px;
  background: #f3f3f3;
  border-radius: 5px;
}
.cartItem span{margin: 5px 10px;}

.removeFCart{
  float: right;
  padding: 5px 10px;
  background-color: red;
  color: white;
  font-size: 11px;
  font-weight: 500;
  border-radius: 3px;
  cursor: pointer;
}

.totalPrice{border-top: 2px solid #d0d0c1;}

.form-style-9{
	max-width: 450px;
	background: #FAFAFA;
	padding: 30px;
	margin: 50px auto;
	box-shadow: 1px 1px 25px rgba(0, 0, 0, 0.35);
}
.form-style-9 ul{
	padding:0;
	margin:0;
	list-style:none;
}
.form-style-9 ul li{
	display: block;
	margin-bottom: 10px;
	min-height: 35px;
}
.form-style-9 ul li  .field-style{
	box-sizing: border-box; 
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box; 
	padding: 8px;
	outline: none;
	border: 1px solid #B0CFE0;
	-webkit-transition: all 0.30s ease-in-out;
	-moz-transition: all 0.30s ease-in-out;
	-ms-transition: all 0.30s ease-in-out;
	-o-transition: all 0.30s ease-in-out;

}.form-style-9 ul li  .field-style:focus{
	box-shadow: 0 0 5px #B0CFE0;
	border:1px solid #B0CFE0;
}
.form-style-9 ul li .field-split{
	width: 49%;
}
.form-style-9 ul li .field-full{
	width: 100%;
}
.form-style-9 ul li input.align-left{
	float:left;
}
.form-style-9 ul li input.align-right{
	float:right;
}
.form-style-9 ul li textarea{
	width: 100%;
	height: 100px;
}
.form-style-9 ul li input[type="button"], 
.form-style-9 ul li input[type="submit"] {
  border: 0;
  display: block;
  width:140px;
  margin: 10px auto;
  text-align: center;
  background: #86bc25;
  box-shadow: 1px -2px 4px 2px #777777;
  border-radius: 10px;
  padding: 10px 15px 10px 15px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
}
.form-style-9 ul li input[type="button"]:hover, 
.form-style-9 ul li input[type="submit"]:hover { color: #fff;
	background-color: #1a485e;
}